function setupIntroSection()
{
    const topSection = document.querySelector("section#top") as HTMLElement
    const topHeaderContents = `  <strong><h2>Joshua V Petersen</h2></strong>
                    <p> Programmer</p><br>
                    <h3> SECOND YEAR UNDERGRADUATE<br>
                        BSC (Hons) Computer Games Programming<br>
                        University of Gloucestershire</h3>`
    const topHeader = topSection.querySelector("header") as HTMLElement
    topHeader.innerHTML = topHeaderContents


    const topFooterConents = ` <footer>
                <a href="#contact" class="button scrolly">Contact Me</a>
            </footer>
`
    const topFooter = topSection.querySelector("footer") as HTMLElement
    topFooter.innerHTML = topFooterConents

}


function goBack()
{
    if(window.history.length > 1)
    {
        window.history.back()
    }
    else
    {
        window.location.href = "index.html"
    }
}

function setupNavigation()
{
    
    
    const navigation = document.querySelector("nav#nav") as HTMLElement
    navigation.innerHTML = ` <ul>
                <li class="two-nav-buttons"><a href="#top" id="top-link"><span class="icon solid fa-home">Intro</span></a></li>
                <li><a href="#portfolio" id="portfolio-link"><span class="icon solid fa-th">Portfolio</span></a></li>
                <li><a href="#about" id="about-link"><span class="icon solid fa-user">About Me</span></a></li>
                <li><a href="#contact" id="contact-link"><span class="icon solid fa-envelope">Contact</span></a></li>
                <li class="hidden"><a id="back"><span class="icon solid fa-arrow-left">Back</span></a></li>
            </ul>`
    const back = navigation.querySelector("a#back") as HTMLAnchorElement
    alert(`Is index: ${document.URL}`)
    alert(`Is index: ${!document.URL.includes("html")}`)
    alert(`Length: ${document.URL.length}`)
    if(document.URL.includes(".html"))
    {
        const li = back.parentElement as HTMLElement
        li.classList.remove("hidden")
        back.addEventListener("click", goBack)
    }
    
}

function onDomContentLoaded()
{
    setupIntroSection()
    setupNavigation()
}


document.addEventListener("DOMContentLoaded", onDomContentLoaded)